import {
  Row,
  Col,
  Card,
  Form,
  Tag,
  Radio,
  Table,
  Upload,
  Space,
  message,
  Progress,
  Checkbox,
  Modal,
  Button,
  Avatar,
  Select,
  Input,
  Typography,
} from "antd";
import moment from "moment";
import Axios from "axios";
import { useState, useEffect } from "react";
import { BASE_URL } from "../../utills/appConstant";

const { Title } = Typography;

function AddQrCode({ handleCancel }) {
  const [type, setType] = useState("upi_qr");
  const [name, setName] = useState("");
  const [paymentAmount, setPaymentAmount] = useState(1);
  const [description, setDescription] = useState("");
  const [purpose, setPurpose] = useState("");

  const onChange = (e) => console.log(`radio checked:${e.target.value}`);

  const [usage, setUsage] = useState("multiple_use");
  const [fixed, setFixed] = useState(1);

  //Add data in the table
  const postData = () => {
    var check;
    if (fixed == 1) {
      check = false;
    } else {
      check = true;
    }
    const qr_data = {
      type: type,
      name: name,
      usage: usage,
      fixed_amount: check,
      payment_amount: paymentAmount*100,
      description: description,
      close_by: 1981615838,
      notes: {
        purpose: purpose,
      },
    };
    Axios.post(BASE_URL+"/qr_codes", qr_data);
    console.log(qr_data);
    handleCancel();
  };
  const close = () => {
    handleCancel();
  };
  return (
    <>
      <Form>
        <Row>
          <Col>
            <label style={{ marginLeft: "8px" }}>QR Usage</label>
            <br></br>
            <Radio.Group
              onChange={(e) => setUsage(e.target.value)}
              value={usage}
              style={{ marginLeft: "8px", marginTop: "15px" }}
            >
              <Radio value={"multiple_use"}>Multiple Payment</Radio>
              <Radio value={"single_use"}>Single Payment</Radio>
            </Radio.Group>
          </Col>
        </Row>

        <Row style={{ marginTop: "15px" }}>
          <Col>
            <label style={{ marginLeft: "8px" }}>
              Accept only fixed amount on this QR?
            </label>
            <br></br>
            <Radio.Group
              onChange={(e) => setFixed(e.target.value)}
              value={fixed}
              style={{ marginLeft: "8px", marginTop: "15px" }}
            >
              <Radio value={1}>No</Radio>
              <Radio value={2}>Yes</Radio>
            </Radio.Group>
          </Col>
        </Row>
        {fixed == 2 ? (
          <>
            <Row style={{ marginTop: "15px" }}>
              <Col>
                <label style={{ marginLeft: "8px" }}>Amount</label>
                <br />
                <Input
                  placeholder="Enter the Fixed Amount"
                  type="number"
                  size="small"
                  onChange={(e) => setPaymentAmount(e.target.value)}
                  value={paymentAmount}
                  style={{ marginLeft: "8px", marginTop: "5px" }}
                />
              </Col>
            </Row>
          </>
        ) : (
          <></>
        )}
        <Row style={{ marginTop: "15px" }}>
          <Col xl={24}>
            <label style={{ marginLeft: "8px" }}>Descrpition (Optional)</label>
            <br />
            <Input
              placeholder="Descrpition will be visible in QR Code"
              size="small"
              onChange={(e) => setDescription(e.target.value)}
              value={description}
              style={{ marginLeft: "8px", marginTop: "5px" }}
            />
          </Col>
        </Row>
        <Row style={{ marginTop: "15px" }}>
          <Col xl={24}>
            <label style={{ marginLeft: "8px" }}>QR Name (Optional)</label>
            <br />
            <Input
              placeholder="QR Name will be visible in QR Code"
              size="small"
              onChange={(e) => setName(e.target.value)}
              value={name}
              style={{ marginLeft: "8px", marginTop: "5px" }}
            />
          </Col>
        </Row>
        <Row>
          <Col></Col>
        </Row>
      </Form>
      <br></br>
      <Row>
        <Col style={{ textAlign: "end" }}>
          <Button
            size="small"
            onClick={close}
            style={{ height: "50px", alignItems: "center" }}
          >
            Cancel
          </Button>
          &nbsp;
          <Button
            type="primary"
            size="small"
            onClick={postData}
            style={{ height: "50px", alignItems: "center" }}
          >
            Add Payment Link
          </Button>
        </Col>
      </Row>
    </>
  );
}

export default AddQrCode;
