import { BrowserRouter,Switch, Route } from "react-router-dom";
import Home from "./pages/Home";
import Tables from "./pages/UserMaster";
import Catalogue from "./pages/catalogue/Catalogue";
import AddCatalogue from "./pages/catalogue/addCatalogue";
import Credit from "./pages/Credit";
import Debit from "./pages/Debit";
import BranchMaster from "./pages/BranchMaster";
import SignUp from "./pages/SignUp";
import SignIn from "./pages/SignIn";
import Payment from "./pages/Payment";
import React, { useState } from "react";
import AddBank from "./pages/AddBank";
import Settlement from "./pages/Settlement";
import Main from "./components/layout/Main";
import "antd/dist/antd.css";
import "./assets/styles/main.css";
import "./assets/styles/responsive.css";
import QRCode from "./pages/Qrcode";
import ViewQR from "./pages/ViewQRCode";
import DurationMaster from "./pages/DurationMaster";
import ChechOutPage from "./pages/CheckOutpage";
import Refund from "./pages/Refund";
import Gateway from "./pages/Gateway";
import Dispute from "./pages/dispute";
import Transaction from "./pages/transaction/transaction";
import PaymentLink from "./pages/payment_link/managePaymentLink";
import ManageQrList from "./pages/qrCode/manageQrCode";
import manageBranchMaster from "./pages/branchMaster/manageBranchMaster";
import ManagePaymentButton from "./pages/paymentButton/manageButton";
import ManageSettlement from "./pages/settlement/manageSettlement";
import DemoFile from "./pages/demo/demoFile";
import ManageDemo from "./pages/demo/manageDemo";

function App() {
  // const [token, setToken] = useState();

  // if (!token) {
  //   return <SignIn setToken={setToken} />;
  // }
  return (
    <div className="App">
      <BrowserRouter>
        <Switch>
          <Route path="/sign-up" exact component={SignUp} />
          <Route path="/" exact component={SignIn} />
          <Main>
            <Route exact path="/dashboard" component={Home} />
            <Route exact path="/settlement" component={Settlement} />
            <Route exact path="/manage-settlement" component={ManageSettlement} />
            <Route exact path="/userMaster" component={Tables} />
            <Route exact path="/refund" component={Refund} />
            <Route exact path="/catalogue" component={Catalogue} />
            <Route exact path="/addCatalogue" component={AddCatalogue} />
            <Route exact path="/branchMaster" component={BranchMaster} />
            <Route exact path="/transaction" component={Transaction} />
            <Route exact path="/debit" component={Debit} />
            <Route exact path="/payment" component={Payment} />
            <Route exact path="/addBank" component={AddBank} />
            <Route exact path="/qrcode" component={QRCode} />
            <Route exact path="/viewQR" component={ViewQR} />
            <Route exact path="/durationMaster" component={DurationMaster} />
            <Route exact path="/checkOutpage" component={ChechOutPage} />
            <Route exact path="/gateway" component={Gateway} />
            <Route exact path="/dispute" component={Dispute} />
            <Route exact path="/manage-payment-link" component={PaymentLink} />
            <Route exact path="/manage-qr-code" component={ManageQrList} />
            <Route exact path="/manageBranchMaster" component={manageBranchMaster}></Route>
            <Route exact path="/manageButton" component={ManagePaymentButton}></Route>

            <Route exact path="/demoFile" component={DemoFile }></Route>
            <Route exact path="/manageDemo" component={ManageDemo }></Route>
          </Main>
        </Switch>
      </BrowserRouter>
    </div>
  );
}

export default App;
