import {
  Row,
  Col,
  Card,
  Form,
  Tag,
  Radio,
  Table,
  Upload,
  Space,
  message,
  Progress,
  Checkbox,
  Modal,
  Button,
  Avatar,
  Select,
  Input,
  Typography,
} from "antd";
import Axios from "axios";

import { useState, useEffect } from "react";
import { SearchOutlined, EyeOutlined } from "@ant-design/icons";
import { Chip, Icon, IconButton } from "@mui/material";
import AddQrCode from "./addQrCode";
import ViewQrCode from "./viewQrCode";
import { BASE_URL } from "../../utills/appConstant";

function ManageQrList() {
  const [APIData, setAPIData] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [showForm, setShowForm] = useState(false);
  const [obj, setObj] = useState(null);

  const columns = [
    {
      title: "QR Code Id",
      dataIndex: "id",
      key: "id",
    },
    {
      title: "Description",
      dataIndex: "description",
      key: "description",
    },
    {
      title: "QR Usage",
      dataIndex: "usage",
      key: "usage",
    },
    {
      title: "Amount Received",
      dataIndex: "payments_amount_received",
      key: "payments_amount_received",
    },
    {
      title: "Status",
      key: "status",
      dataIndex: "status",
    },
    {
      title: "Action",
      key: "action",
      dataIndex: "action",
    },
  ];

  const handleshow = (row) => {
    setObj(row);
    setShowForm(true);
  };

  const handleClose = () => {
    setShowForm(false);
    getData();
  };

  const getData = async () => {
    await Axios.get(BASE_URL+"/all_qr_codes").then((res) => {
      setAPIData(
        res.data.items.map((row) => ({
          id: row.id,
          description: row.description,
          payments_amount_received: <>₹ {row.payments_amount_received}</>,
          // customer: row.customer.name,
          usage: row.usage,
          status: (
            <>
              {row.status == "active" ? (
                <>
                  <Chip size="small" label="Active" color="success" />
                </>
              ) : (
                <>
                  {" "}
                  <Chip size="small" label="Closed" color="warning" />
                </>
              )}
            </>
          ),
          action: (
            <>
              <IconButton onClick={() => handleshow(row)}>
                <EyeOutlined style={{ fontSize: "18px" }}></EyeOutlined>
              </IconButton>
            </>
          ),
        }))
      );
    });
  };

  const showModal = () => {
    setIsModalOpen(true);
  };

  const handleOk = () => {
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
    getData();
  };

  useEffect(() => {
    getData();
  }, []);

  return (
    <>
      <Row>
        <Col>
          <Button
            type="primary"
            size="large"
            onClick={showModal}
            style={{ height: "50px" }}
          >
            Create QR Code
          </Button>
        </Col>
        <Col xl={19} style={{ marginLeft: "10px" }}>
          <Input prefix={<SearchOutlined />} placeholder="Search" />
        </Col>
      </Row>
      <br></br>
      <Row>
        <Col xl={4}>
          <label style={{ marginLeft: "8px" }}>QR Code Status</label>
          <Input
            placeholder="Status"
            size="small"
            style={{ marginLeft: "8px", marginTop: "5px" }}
          />
        </Col>
        &nbsp;
        <Col xl={4}>
          <label style={{ marginLeft: "8px" }}>QR Code ID</label>
          <Input
            type="text"
            placeholder="Search By Payment Id"
            size="small"
            style={{ marginLeft: "8px", marginTop: "5px" }}
          />
        </Col>
        &nbsp;
        <Col xl={4}>
          <label style={{ marginLeft: "8px" }}>Customer Email</label>
          <Input
            placeholder="Search By Email"
            size="small"
            style={{ marginLeft: "8px", marginTop: "5px" }}
          />
        </Col>
        &nbsp;
        <Col xl={4}>
          <label style={{ marginLeft: "8px" }}>Customer Contact</label>
          <Input
            type="number"
            placeholder="Search By Phone"
            min={25}
            size="small"
            style={{ marginLeft: "8px", marginTop: "5px" }}
          />
        </Col>
        &nbsp;
        <Col xl={3}>
          <label style={{ marginLeft: "8px" }}>Note</label>
          <Input
            type="number"
            placeholder="Enter Limit"
            min={25}
            size="small"
            style={{ marginLeft: "8px", marginTop: "5px" }}
          />
        </Col>
        &nbsp;&nbsp;&nbsp;
        <Col style={{ marginTop: "28px" }}>
          <Button type="primary">Serach</Button>&nbsp;
          <Button type="secondary">Clear</Button>
        </Col>
      </Row>
      <div className="tabled" style={{ marginTop: "30px" }}>
        <Row gutter={[24, 0]}>
          <Col xs="24" xl={24}>
            <Card bordered={false} className="criclebox tablespace mb-24">
              <div className="table-responsive">
                <Table
                  columns={columns}
                  dataSource={APIData}
                  pagination={false}
                  rowKey="id"
                  className="ant-border-space"
                />
              </div>
            </Card>
          </Col>
        </Row>
      </div>
      <Modal
        title="Payment Link"
        style={{
          top: 20,
        }}
        footer={null}
        visible={isModalOpen}
        onOk={handleOk}
      >
        <AddQrCode handleCancel={handleCancel}></AddQrCode>
      </Modal>
      <Modal
          title="View Payment Link Detail's"
   
          style={{
            top: 20,
          }}
          visible={showForm}
      
          onCancel={handleClose}
        >
          <ViewQrCode
            theViewData={obj}
            handleCancel={handleClose}
          ></ViewQrCode>
        </Modal>
    </>
  );
}

export default ManageQrList;
