import { useState } from "react";
import { Tabs } from "antd";
import Credit from "../Credit";
import Debit from "../Debit";
import AllTransaction from "./allTransaction";
import QRTransaction from "./qr_Payment";

const { TabPane } = Tabs;

const Transaction = () => {
  const [key, setKey] = useState("1");
  
  const callback = (k) => {
    localStorage.setItem("payment-tab", k);
    setKey(k);
  };

  const items = [
    {
      key: "1",
      label: `Tab 1`,
      children: `Content of Tab Pane 1`,
    },
    {
      key: "2",
      label: `Tab 2`,
      children: `Content of Tab Pane 2`,
    },
    {
      key: "3",
      label: `Tab 3`,
      children: `Content of Tab Pane 3`,
    },
  ];
  return (
    <>
      <Tabs defaultActiveKey={localStorage.getItem("payment-tab")} onChange={callback}>
        <TabPane tab="Transaction" key="tab-a">
          {/* <AddBank></AddBank> */}
          <AllTransaction></AllTransaction>
        </TabPane>
        <TabPane tab="Payment Recieved via QR Code" key="tab-b">
          <QRTransaction></QRTransaction>
        </TabPane>
        <TabPane tab="Payment Recieved via Link " key="tab-c">
          <Credit></Credit>
        </TabPane>
        <TabPane tab="Debit" key="tab-d">
          <Debit></Debit>
        </TabPane>
      </Tabs>
    </>
  );
};
export default Transaction;
