import React, { useEffect } from "react";
import ReactWhatsapp from "react-whatsapp";
import {
  EditOutlined,
  EllipsisOutlined,
  SettingOutlined,
  WhatsAppOutlined,
} from "@ant-design/icons";
import {
  Row,
  Col,
  Card,
  Checkbox,
  Modal,
  Form,
  Select,
  Input,
  Button,
  List,
  Tabs,
  Descriptions,
  Avatar,
} from "antd";
import { useState } from "react";
import { CopyToClipboard } from "react-copy-to-clipboard";

const { TabPane } = Tabs;
const { TextArea } = Input;

const ManagePaymentButton = () => {
  var data;
  const getButton = () => {
    alert("Payment Button Created Succefully");
    const rzpPaymentForm = document.getElementById("rzp_payment_form");

    if (!rzpPaymentForm.hasChildNodes()) {
      const script = document.createElement("script");
      script.src = "https://checkout.razorpay.com/v1/payment-button.js";
      script.async = true;
      script.dataset.payment_button_id = "pl_LGeTghOkNTWnxm";
      rzpPaymentForm.appendChild(script);
    }
    console.log(rzpPaymentForm);
    data = rzpPaymentForm;
  };
  //   useEffect(() => {
  //     getButton();
  //   });
  const [showImage, setshowImage] = useState(false);

  const IDIRECTION = {
    accountNo: 39477383399,
    accountHolderName: "Sunil Bengade",
    branchName: "Navi MUMBAI",
    ifsc: "SBIN0012965",
    registeredMobile: 7715815877,
    email: "admin@idirection.in",
  };
  const [size, setSize] = useState("default");
  const [bankName, setBankName] = useState("Select the Bank");

  const bankNames = [
    { value: "Select the Bank", label: "Select the Bank" },
    { value: "IDIRECTION (SBI)", label: "IDIRECTION (SBI)" },
  ];

  return (
    <div>
      <Card
        bordered={false}
        className="criclebox tablespace mb-24"
        title={
          <>
            <Row gutter={[24, 0]} className="ant-row-flex ant-row-flex-middle">
              <Col xs={24} md={10} style={{ textAlign: "end" }}>
                <h6 className="font-semibold m-0">Select Bank</h6>
              </Col>
              <Col xs={24} md={10}>
                <Select
                  className="font-semibold m-0"
                  options={bankNames}
                  value={bankName}
                  size="large"
                  onChange={(e) => {
                    setBankName(e);
                  }}
                  placeholder="Select Bank"
                  style={{ width: "200px" }}
                ></Select>
              </Col>
              <Col xs={24} md={4}>
                <Button
                  type="primary"
                  size="small"
                  onClick={getButton}
                  style={{
                    height: "40px",
                    marginTop: "2px",
                    marginLeft: "20px",
                  }}
                >
                  Generate Button{" "}
                </Button>
              </Col>
            </Row>
            {bankName == "IDIRECTION (SBI)" ? (
              <div>
                <br></br>
                {showImage ? (
                  <>fdf</>
                ) : (
                  <>
                    <Descriptions
                      bordered
                      title="Bank Detail's"
                      size={size}
                      // extra={<Button type="primary">Edit</Button>}
                    >
                      <Descriptions.Item label="Bank Name">
                        State Bank of India
                      </Descriptions.Item>
                      <Descriptions.Item label="Name">
                        {IDIRECTION.accountHolderName}
                      </Descriptions.Item>
                      <Descriptions.Item label="Account No" span={3}>
                        {IDIRECTION.accountNo}
                      </Descriptions.Item>
                      <Descriptions.Item label="Branch Name">
                        {IDIRECTION.branchName}
                      </Descriptions.Item>
                      <Descriptions.Item label="IFSC ">
                        {IDIRECTION.ifsc}
                      </Descriptions.Item>
                      <Descriptions.Item label="Registered Mobile">
                        {IDIRECTION.registeredMobile}
                      </Descriptions.Item>
                      <Descriptions.Item label="Registered Email">
                        {IDIRECTION.email}
                        <br></br>
                      </Descriptions.Item>
                    </Descriptions>
                    <br></br>
                    <div>
                      <br></br>
                      <div style={{ textAlign: "center" }}>
                        {" "}
                        <form id="rzp_payment_form"></form>
                      </div>
                    </div>
                  </>
                )}{" "}
              </div>
            ) : (
              <></>
            )}
          </>
        }
      ></Card>
    </div>
  );
};

export default ManagePaymentButton;
