import {
  Row,
  Col,
  Card,
  Form,
  Tag,
  Radio,
  Table,
  Upload,
  Space,
  message,
  Progress,
  Checkbox,
  Modal,
  Button,
  Avatar,
  Select,
  Input,
  Typography,
} from "antd";
import Axios from "axios";

import { useState, useEffect } from "react";
import { SearchOutlined, EyeOutlined } from "@ant-design/icons";
import { Chip, Icon, IconButton } from "@mui/material";
import { BASE_URL } from "../../utills/appConstant";

function AllTransaction() {
  const [APIData, setAPIData] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [showForm, setShowForm] = useState(false);
  const [obj, setObj] = useState(null);

  const columns = [
    {
      title: "Payment Link Id",
      dataIndex: "id",
      key: "id",
    },
    {
      title: "Currency",
      dataIndex: "currency",
      key: "currency",
    },
    {
      title: "Amount",
      dataIndex: "amount",
      key: "amount",
    },
    {
      title: "Payment Mode",
      dataIndex: "method",
      key: "method",
    },

    {
      title: "Email",
      dataIndex: "email",
      key: "email",
    },
    {
      title: "Contact",
      dataIndex: "contact",
      key: "contact",
    },
    {
      title: "Status",
      key: "status",
      dataIndex: "status",
    },
    // {
    //   title: "Action",
    //   key: "action",
    //   dataIndex: "action",
    // },
  ];

  const handleshow = (row) => {
    setObj(row);
    setShowForm(true);
  };

  const handleClose = () => {
    setShowForm(false);
    getData();
  };
  const getData = async () => {
    await Axios.get(BASE_URL + "/payments").then((res) => {
      setAPIData(
        res.data.items.map((row) => ({
          id: <a>{row.id}</a>,
          currency: row.currency,
          amount: (
            <>
              ₹ <span>{row.amount / 100}</span>
            </>
          ),
          method: row.method,
          wallet: row.wallet,
          email: row.email,
          contact: row.contact,
          status: (
            <>
              {row.status == "captured" ? (
                <>
                  <Chip size="small" label="Captured" color="success" />
                </>
              ) : (
                <>
                  {" "}
                  <Chip size="small" label="Failed" color="warning" />
                </>
              )}
            </>
          ),
          // action: (
          //   <>
          //     <IconButton onClick={() => handleshow(row)}>
          //       <EyeOutlined style={{ fontSize: "18px" }}></EyeOutlined>
          //     </IconButton>
          //   </>
          // ),
        }))
      );
    });
  };

  useEffect(() => {
    getData();
  }, []);

  return (
    <>
      <Row>

        <Col xl={22} style={{ marginLeft: "10px" }}>
          <Input prefix={<SearchOutlined />} placeholder="Search" />
        </Col>
      </Row>
      <div className="tabled" style={{ marginTop: "30px" }}>
        <Row gutter={[24, 0]}>
          <Col xs="24" xl={24}>
            <Card bordered={false} className="criclebox tablespace mb-24">
              <div className="table-responsive">
                <Table
                  columns={columns}
                  dataSource={APIData}
                  pagination={false}
                  rowKey="id"
                  className="ant-border-space"
                />
              </div>
            </Card>
          </Col>
        </Row>
      </div>
    </>
  );
}

export default AllTransaction;
