import {
  Row,
  Col,
  Card,
  Form,
  Tag,
  Radio,
  Table,
  Upload,
  Space,
  message,
  Progress,
  Checkbox,
  Modal,
  Button,
  Avatar,
  Select,
  Input,
  Typography,
} from "antd";
import Axios from "axios";

import { useState, useEffect } from "react";
import { SearchOutlined, EyeOutlined, SendOutlined } from "@ant-design/icons";
import { Chip, Icon, IconButton } from "@mui/material";
import AddPaymentLink from "./addPaymentLink";
import ViewPaymentLink from "./viewPaymentLink";
import Notify from "./notify";
import { BASE_URL } from "../../utills/appConstant";

function PaymentLink() {
  const [APIData, setAPIData] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [showForm, setShowForm] = useState(false);
  const [obj, setObj] = useState(null);

  const columns = [
    {
      title: "Payment Link Id",
      dataIndex: "id",
      key: "id",
    },
    {
      title: "Currency",
      dataIndex: "currency",
      key: "currency",
    },
    {
      title: "Amount",
      dataIndex: "amount",
      key: "amount",
    },
    {
      title: "Customer",
      dataIndex: "customer",
      key: "customer",
    },

    {
      title: "Payment Link",
      dataIndex: "short_url",
      key: "short_url",
    },
    {
      title: "Status",
      key: "status",
      dataIndex: "status",
    },
    {
      title: "Action",
      key: "action",
      dataIndex: "action",
    },
  ];

  const handleshow = (row) => {
    setObj(row);
    setShowForm(true);
  };

  const handleClose = () => {
    setShowForm(false);
    getData();
  };

  const [showForm1, setShowForm1] = useState(false);
  const [obj1, setObj1] = useState(null);
  const handleNotify = (row) => {
    setObj1(row);
    setShowForm1(true);
  };

  const CloseNotify = () => {
    setShowForm1(false);
    getData();
  };
  const getData = async () => {
    await Axios.get(BASE_URL+"/payment_links/").then((res) => {
      setAPIData(
        res.data.payment_links.map((row) => ({
          id: row.id,
          currency: row.currency,
          amount: (
            <>
              ₹ <span>{row.amount / 100}</span>
            </>
          ),
          customer: row.customer.name,
          short_url: <a>{row.short_url}</a>,
          status: (
            <>
              {row.status == "created" ? (
                <>
                  <Chip size="small" label="Created" color="success" />
                </>
              ) : (
                <>
                  {" "}
                  <Chip size="small" label="Cancelled" color="warning" />
                </>
              )}
            </>
          ),
          action: (
            <>
              <IconButton onClick={() => handleshow(row)}>
                <EyeOutlined style={{ fontSize: "18px" }}></EyeOutlined>
              </IconButton>
              <IconButton onClick={() => handleNotify(row)}>
                <SendOutlined style={{ fontSize: "18px" }}></SendOutlined>
              </IconButton>
            </>
          ),
        }))
      );
    });
  };

  const showModal = () => {
    setIsModalOpen(true);
  };

  const handleOk = () => {
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
    getData();
  };

  useEffect(() => {
    getData();
  }, []);

  return (
    <>
      <Row>
        <Col>
          <Button
            type="primary"
            size="large"
            onClick={showModal}
            style={{ height: "50px" }}
          >
            Create Payment Link
          </Button>
        </Col>
        <Col xl={19} style={{ marginLeft: "10px" }}>
          <Input prefix={<SearchOutlined />} placeholder="Search" />
        </Col>
      </Row>
      <br></br>
      <Row>
        <Col xl={4}>
          <label style={{ marginLeft: "8px" }}>Payment Link Status</label>
          <Input
            placeholder="Status"
            size="small"
            style={{ marginLeft: "8px", marginTop: "5px" }}
          />
        </Col>
        &nbsp;
        <Col xl={4}>
          <label style={{ marginLeft: "8px" }}>Payment Link ID</label>
          <Input
            type="text"
            placeholder="Search By Payment Id"
            size="small"
            style={{ marginLeft: "8px", marginTop: "5px" }}
          />
        </Col>
        &nbsp;
        <Col xl={4}>
          <label style={{ marginLeft: "8px" }}>Customer Email</label>
          <Input
            placeholder="Search By Email"
            size="small"
            style={{ marginLeft: "8px", marginTop: "5px" }}
          />
        </Col>
        &nbsp;
        <Col xl={4}>
          <label style={{ marginLeft: "8px" }}>Customer Contact</label>
          <Input
            type="number"
            placeholder="Search By Phone"
            min={25}
            size="small"
            style={{ marginLeft: "8px", marginTop: "5px" }}
          />
        </Col>
        &nbsp;
        <Col xl={3}>
          <label style={{ marginLeft: "8px" }}>Count</label>
          <Input
            type="number"
            placeholder="Enter Limit"
            min={25}
            size="small"
            style={{ marginLeft: "8px", marginTop: "5px" }}
          />
        </Col>
        &nbsp;&nbsp;&nbsp;
        <Col style={{ marginTop: "28px" }}>
          <Button type="primary">Serach</Button>&nbsp;
          <Button type="secondary">Clear</Button>
        </Col>
      </Row>
      <div className="tabled" style={{ marginTop: "30px" }}>
        <Row gutter={[24, 0]}>
          <Col xs="24" xl={24}>
            <Card bordered={false} className="criclebox tablespace mb-24">
              <div className="table-responsive">
                <Table
                  columns={columns}
                  dataSource={APIData}
                  pagination={false}
                  rowKey="id"
                  className="ant-border-space"
                />
              </div>
            </Card>
          </Col>
        </Row>
      </div>
      <Modal
        title="Payment Link"
        style={{
          top: 20,
        }}
        footer={null}
        visible={isModalOpen}
        onOk={handleOk}
      >
        <AddPaymentLink handleCancel={handleCancel}></AddPaymentLink>
      </Modal>
      <Modal
        title="View Payment Link Detail's"
        style={{
          top: 20,
        }}
        visible={showForm}
        onCancel={handleClose}
      >
        <ViewPaymentLink
          theViewData={obj}
          handleCancel={handleClose}
        ></ViewPaymentLink>
      </Modal>

      <Modal
        title="Notification Reminder"
        visible={showForm1}
        footer={null}
        onCancel={CloseNotify}
      >
        <Notify NotifyData={obj1} handleCancel={CloseNotify}></Notify>
      </Modal>
    </>
  );
}

export default PaymentLink;
