import {
    Row,
    Col,
    Card,
    Form,
    Breadcrumb,
    Table,
    Upload,
    Space,
    message,
    Progress,
    Modal,
    Button,
    Avatar,
    Select,
    Input,
    Typography,
} from "antd";

import { useState, useEffect } from "react";
import Axios from "axios";

import { Link } from "react-router-dom";

const { TextArea } = Input;

function DemoFile() {
    const option = [
        { value: "A", label: "A" },
        { value: "B", label: "B" },
        { value: "C", label: "C" },
        { value: "D", label: "D" }
    ];
    const [startSlab, setStartSlab] = useState("");
    const [endSlab, setEndSlab] = useState("");
    const [title, setTitle] = useState("");
    const [value, setValue] = useState("");

    const base_url = 'http://localhost:3000/matrixs'

    const postData = () => {
        const catData = {
            title: title,
            start_Slab: startSlab,
            end_Slab: endSlab,
            value: value,
        };
        console.log(catData);
        Axios.post(base_url + "/postMatrix", catData);
    };
    return (
        <>
            <Card>
                <Form>
                    <Row>
                        <Col xl={24}>
                            <p >Title</p>
                            <Select
                                required
                                placeholder="Select Type"
                                size="large"
                                options={option}
                                value={title}
                                onChange={(e) => setTitle(e)}
                                style={{ width: "100%", marginTop: "-14px" }}
                            />
                        </Col>

                    </Row>
                    <Row>
                        <Col xl={11} style={{ marginTop: "7px" }}>
                            <label>Start Slab</label>
                            <Input
                                required
                                value={startSlab}
                                onChange={(e) => setStartSlab(e.target.value)}
                                placeholder="Enter Name"
                            />
                        </Col> &nbsp;
                        <Col xl={12} style={{ marginTop: "7px" }}>
                            <label>End Slab</label>
                            <Input
                                required
                                value={endSlab}
                                onChange={(e) => setEndSlab(e.target.value)}
                                placeholder="Enter Name"
                            />
                        </Col>
                    </Row>
                    <Row style={{ marginTop: "10px" }}>

                        <Col xl={11}>
                            <label style={{ marginLeft: "13px" }}>Value</label>
                            <Input
                                required
                                type="number"
                                value={value}
                                onChange={(e) => setValue(e.target.value)}
                                placeholder="Enter the Value"
                                style={{ marginLeft: "13px" }}
                            />
                        </Col>
                    </Row>
                    <Row style={{ marginTop: "10px", textAlign: "center" }}>
                        <Col span={24}>
                            <Link to={"/catalogue"}>
                                <Button color="gray-7">Cancel</Button>
                            </Link>
                            &nbsp;

                            <Button htmlType="submit" type="primary" onClick={postData}>
                                Save
                            </Button>
                        </Col>
                    </Row>
                </Form>
            </Card>
        </>
    );
}

export default DemoFile;
