import {
  Row,
  Col,
  Card,
  Form,
  Breadcrumb,
  Table,
  Upload,
  Space,
  message,
  Progress,
  Modal,
  Button,
  Avatar,
  Select,
  Input,
  Typography,
} from "antd";

import { useState, useEffect } from "react";
import Axios from "axios";

import { Link } from "react-router-dom";

const { TextArea } = Input;

function AddCatalogue() {
  const option = [
    { value: "Equity", label: "Equity" },
    { value: "Future", label: "Future" },
    { value: "Options", label: "Options" },
  ];
  const [catalogueName, setCatalogueName] = useState("");
  const [duration, setDuration] = useState("");
  const [type, setType] = useState("");
  const [price, setPrice] = useState(0);

  const base_url = 'http://43.204.212.53:5002'
  const postData = () => {
    const catData = {
      catalogue_name: catalogueName,
      duration: duration,
      type: type,
      price: price,
    };
    console.log(catData);
    Axios.post(base_url + "/services/add_catalogue", catData);
  };
  const [validated, setValidated] = useState(false);
  const handleSubmit = (event) => {
    const form = event.currentTarget;
    if (form.checkValidity() === false) {
      event.preventDefault();
      event.stopPropagation();
      // alert("Please Provide appropriate Data")
    } else if (form.checkValidity() != false) {
      postData();
      //   changePage();
    }
    setValidated(true);
  };
  return (
    <>
      <Card>
        <Row>
          <Col>
            <Breadcrumb>
              <Breadcrumb.Item>
                <Link to={"/dashboard"}>Dashboard</Link>
              </Breadcrumb.Item>
              <Breadcrumb.Item>
                <Link to={"/catalogue"}>Manage Catalogue</Link>
              </Breadcrumb.Item>
              <Breadcrumb.Item>Add Catalogue</Breadcrumb.Item>
            </Breadcrumb>
          </Col>
        </Row>
        <br></br>

        <Form>
          <Row>
            <Col xl={12} style={{ marginTop: "7px" }}>
              <label>Name</label>
              <Input
                required
                value={catalogueName}
                onChange={(e) => setCatalogueName(e.target.value)}
                placeholder="Enter Name"
              />
            </Col>
            <Col xl={12}>
              <p style={{ marginLeft: "13px" }}>Type</p>
              <Select
                required
                placeholder="Select Type"
                size="large"
                options={option}
                value={type}
                onChange={(e) => setType(e.target.value)}
                style={{ marginLeft: "15px", width: "90%", marginTop: "-14px" }}
              />
            </Col>
          </Row>
          <Row style={{ marginTop: "10px" }}>
            <Col xl={12}>
              <label>Duration</label>
              <Input
                required
                value={duration}
                onChange={(e) => setDuration(e.target.value)}
                placeholder="Enter Duration"
              />
            </Col>
            <Col xl={11}>
              <label style={{ marginLeft: "13px" }}>Price</label>
              <Input
                required
                type="number"
                min={5000}
                value={price}
                onChange={(e) => setPrice(e.target.value)}
                placeholder="Enter Price"
                style={{ marginLeft: "13px" }}
              />
            </Col>
          </Row>
          <Row style={{ marginTop: "10px" }}>
            <Col xl={23}>
              <label style={{ marginLeft: "13px" }}>Description</label>
              <TextArea placeholder="Enter Description" rows={6} />
            </Col>
          </Row>
          <Row style={{ marginTop: "10px", textAlign: "center" }}>
            <Col span={24}>
              <Link to={"/catalogue"}>
                <Button color="gray-7">Cancel</Button>
              </Link>
              &nbsp;
              {/* <button className="rounded"
              style={{ background: "green", borderColor: "white",fontSize:"22px" }}>
                Submit
              </button> */}
              <Button htmlType="submit" type="primary" onClick={handleSubmit}>
                Save
              </Button>
            </Col>
          </Row>
        </Form>
      </Card>
    </>
  );
}

export default AddCatalogue;
