import {
  Row,
  Col,
  Card,
  Form,
  Tag,
  Radio,
  Table,
  Upload,
  Space,
  message,
  Progress,
  Checkbox,
  Modal,
  Button,
  Avatar,
  Select,
  Input,
  Typography,
} from "antd";
import moment from "moment";
import Axios from "axios";
import { Chip } from "@mui/material";
import { useState, useEffect } from "react";
import { BASE_URL } from "../../utills/appConstant";

const { Title } = Typography;

function ViewPaymentLink({ theViewData, handleCancel }) {
  const [amount, setAmount] = useState(theViewData.amount);
  const [description, setDescription] = useState("");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [contact, setContact] = useState("");
  const [note, setNote] = useState("");

  const close = () => {
    handleCancel();
  };
  const cancelLink = () => {
    Axios.post(BASE_URL+"/payment_links/" + theViewData.id + "/cancel");
    handleCancel();
  };
  return (
    <>
      <Form>
        <br></br>
        <Row>
          <Col xl={12}>
            <label>Payment ID</label>
          </Col>
          <Col xl={12}>
            <b>{theViewData.id}</b>
          </Col>
        </Row>
        <br></br>
        <Row>
          <Col xl={12}>
            <label>Link Type</label>
          </Col>
          <Col xl={12}>
            <label>Standard Payment Link</label>
          </Col>
        </Row>
        <br></br>
        <Row>
          <Col xl={12}>
            <label>Payment For</label>
          </Col>
          <Col xl={12}>
            <label>{theViewData.description}</label>
          </Col>
        </Row>
        <br></br>
        <Row>
          <Col xl={12}>
            <label>Status</label>
          </Col>
          <Col xl={12}>
            <label>
              {theViewData.status == "created" ? (
                <>
                  <Chip size="small" label="Created" color="success" />
                </>
              ) : (
                <>
                  {" "}
                  <Chip size="small" label="Cancelled" color="warning" />
                </>
              )}
            </label>{" "}
            &nbsp;
            <a onClick={cancelLink}>
              <u>Cancel Link</u>
            </a>
          </Col>
        </Row>
        <br></br>
        <Row>
          <Col xl={12}>
            <label>Partial Payment</label>
          </Col>
          <Col xl={12}>
            <label>
              {theViewData.accept_partial == true ? (
                <>
                  <label>Enable</label>
                </>
              ) : (
                <>
                  <label>Disable</label>
                </>
              )}
            </label>
          </Col>
        </Row>
        <br></br>
        <Row>
          <Col xl={12}>
            <label>Amount</label>
          </Col>
          <Col xl={12}>
            <label>₹ {theViewData.amount / 100}</label>
          </Col>
        </Row>
        <br></br>
        <Row>
          <Col xl={12}>
            <label>Link Url</label>
          </Col>
          <Col xl={12}>
            <a>{theViewData.short_url}</a>
          </Col>
        </Row>
        <br></br>
        <Row>
          <Col xl={12}>
            <label>Customer Details</label>
          </Col>
          <Col xl={12}>
            <label>{theViewData.customer.name}</label>
            <br></br>
            <label>{theViewData.customer.email}</label>
            <br></br>
            <label>{theViewData.customer.contact}</label>
          </Col>
        </Row>
        <br></br>
        <Row>
          <Col xl={12}>
            <label>Notes</label>
          </Col>
          <Col xl={12}>
            <label>{theViewData.notes.policy_name}</label>
            {/* <br></br>
            <a>Edit</a> ||&nbsp;
            <a>delete</a> */}
          </Col>
        </Row>
        <br></br>
        <Row>
          <Col xl={12}>
            <label>Created At</label>
          </Col>
          <Col xl={12}>
            <label>
              {new Date(theViewData.created_at).toLocaleDateString("en-GB")}
            </label>
          </Col>
        </Row>
      </Form>
    </>
  );
}

export default ViewPaymentLink;
