import {
  Row,
  Col,
  Card,
  Form,
  Tag,
  Radio,
  Table,
  Upload,
  Space,
  message,
  Progress,
  Checkbox,
  Modal,
  Button,
  Avatar,
  Select,
  Input,
  Typography,
} from "antd";
import moment from "moment";
import Axios from "axios";
import { useState, useEffect } from "react";
import { BASE_URL } from "../../utills/appConstant";

const { Title } = Typography;

function AddPaymentLink({ handleCancel }) {
  const [amount, setAmount] = useState(null);
  const [description, setDescription] = useState("");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [contact, setContact] = useState("");
  const [note, setNote] = useState("");

  const onChange = (e) => console.log(`radio checked:${e.target.value}`);
  const option = [
    { value: "Standard Payment Link", label: "Standard Payment Link" },
    { value: "UPI Payment Link", label: "UPI Payment Link" },
  ];

  //Add data in the table
  const base_url = "http://localhost:5000";
  const postData = () => {
    const payment_data = {
      amount: amount*100, //Amount is in paisa
      currency: "INR",
      description: description,
      customer: {
        name: name,
        email: email,
        contact: contact,
      },
      notify: {
        sms: true,
        email: true,
      },
      notes: {
        policy_name: note,
      },
    };
    Axios.post(BASE_URL + "/standard_payment_links", payment_data);
    handleCancel();
  };
  const close = () => {
    handleCancel();
  };
  return (
    <>
      <Form>
        <Row>
          <Col xl={12}>
            <p style={{ marginLeft: "1px" }}>Type</p>
            <Select
              placeholder="Select Type"
              size="large"
              defaultValue={"Standard Payment Link"}
              options={option}
              style={{ marginLeft: "1px", width: 230, marginTop: "-14px" }}
            />
          </Col>
          <Col xl={12}>
            <label style={{ marginLeft: "8px" }}>Amount</label>
            <Input
              type="number"
              placeholder="Enter the Amount in Paise"
              value={amount}
              onChange={(e) => setAmount(e.target.value)}
              style={{ marginLeft: "8px", marginTop: "5px" }}
            />
          </Col>
        </Row>
        <br></br>
        {/* <Row>
          <Col xl={12}>
            <label style={{ color: "green" }}>
              Coversion :- ₹ {amount / 100}
            </label>
          </Col>
        </Row>
        <br></br> */}
        <Row>
          <Col xl={24}>
            <label>Payment For</label>
            <Input
              placeholder="Enter the Payment Description"
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              style={{ marginTop: "5px" }}
            />
          </Col>
        </Row>
        <br></br>
        <Row>
          <Col xl={11}>
            <label>Customer Name</label>
            <Input
              placeholder="Enter the Name"
              value={name}
              onChange={(e) => setName(e.target.value)}
              style={{ marginTop: "5px" }}
            />
          </Col>
          &nbsp;
          <Col xl={12}>
            <label>Customer Phone</label>
            <Input
              value={contact}
              onChange={(e) => setContact(e.target.value)}
              placeholder="Enter the Phone Number"
              style={{ marginTop: "5px" }}
            />
          </Col>
        </Row>
        <br></br>
        <Row>
          <Col xl={11}>
            <label>Customer Email</label>
            <Input
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="Enter the Email"
              style={{ marginTop: "5px" }}
            />
          </Col>
        </Row>
        <br />
        <Row>
          <Col>
            <Checkbox onChange={onChange} checked>
              Notify via Email
            </Checkbox>
          </Col>
          <Col>
            <Checkbox onChange={onChange} checked>
              Notify via SMS
            </Checkbox>
          </Col>
        </Row>
        <br></br>
        <Row>
          <Col>
            <label>Link Expiry</label>
            <br></br>
            <Checkbox onChange={onChange}>No Expiry</Checkbox>
          </Col>
        </Row>
      </Form>
      <br></br>
      <Row>
        <Col style={{ textAlign: "end" }}>
          <Button
            size="small"
            onClick={close}
            style={{ height: "50px", alignItems: "center" }}
          >
            Cancel
          </Button>
          &nbsp;
          <Button
            type="primary"
            size="small"
            onClick={postData}
            style={{ height: "50px", alignItems: "center" }}
          >
            Add Payment Link
          </Button>
        </Col>
      </Row>
    </>
  );
}

export default AddPaymentLink;
