import {
  Row,
  Col,
  Card,
  Form,
  Tag,
  Radio,
  Table,
  Upload,
  Space,
  message,
  Progress,
  Checkbox,
  Modal,
  Button,
  Avatar,
  Select,
  Input,
  Typography,
} from "antd";
import moment from "moment";
import Axios from "axios";
import { Chip } from "@mui/material";
import { useState, useEffect } from "react";
import { saveAs } from "file-saver";

const { Title } = Typography;

function ViewQrCode({ theViewData, handleCancel }) {
  const [amount, setAmount] = useState(theViewData.amount);
  const [description, setDescription] = useState("");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [contact, setContact] = useState("");
  const [note, setNote] = useState("");

  const close = () => {
    handleCancel();
  };
  const downloadImage = () => {
    saveAs(theViewData.image_url, "QR-CODE.jpg"); // Put your image url here.
  };
  return (
    <>
      <Form>
        <br></br>
        <Row>
          <Col xl={12}>
            <label>QR ID</label>
          </Col>
          <Col xl={12}>
            <b>{theViewData.id}</b>
          </Col>
        </Row>
        <br></br>
        <Row>
          <Col xl={12}>
            <label>Status</label>
          </Col>
          <Col xl={12}>
            <label>
              {theViewData.status == "active" ? (
                <>
                  <Chip size="small" label="Active" color="success" />
                </>
              ) : (
                <>
                  {" "}
                  <Chip size="small" label="Closed" color="warning" />
                </>
              )}
            </label>{" "}
          </Col>
        </Row>
        <br></br>
        <Row>
          <Col xl={12}>
            <label>QR Usage</label>
          </Col>
          <Col xl={12}>
            <label>
              {theViewData.usage == "multiple_use" ? (
                <>
                  <label>Multiple Use</label>
                </>
              ) : (
                <>
                <label>Single Use</label></>
              )}
            </label>
          </Col>
        </Row>
        <br></br>
        <Row>
          <Col xl={12}>
            <label>QR Name</label>
          </Col>
          <Col xl={12}>
            <label>{theViewData.name}</label>
          </Col>
        </Row>
        {theViewData.payment_amount == null ? (
          <></>
        ) : (
          <>
            <br></br>
            <Row>
              <Col xl={12}>
                <label>Payment Amount</label>
              </Col>
              <Col xl={12}>
                <label>₹ {theViewData.payment_amount}</label>
              </Col>
            </Row>
          </>
        )}
        <br></br>
        <Row>
          <Col xl={12}>
            <label>Amount Recieved</label>
          </Col>
          <Col xl={12}>
            <label>₹ {theViewData.payments_amount_received}</label>
          </Col>
        </Row>
        <br></br>
        <Row>
          <Col xl={12}>
            <label>No of Payment</label>
          </Col>
          <Col xl={12}>
            <a>{theViewData.payments_count_received}</a>
          </Col>
        </Row>
        <br></br>
        <Row>
          <Col xl={12}>
            <label>Description</label>
          </Col>
          <Col xl={12}>
            <label>{theViewData.description}</label>
            {/* <br></br>
              <a>Edit</a> ||&nbsp;
              <a>delete</a> */}
          </Col>
        </Row>
        <br></br>
        <Row>
          <Col xl={12}>
            <label>Created At</label>
          </Col>
          <Col xl={12}>
            <label>
              {new Date(theViewData.created_at).toLocaleDateString("en-GB")}
            </label>
          </Col>
        </Row>
        <br></br>
        <Row>
          <Col xl={12}>
            <label>Download QR Code</label>
          </Col>
          <Col xl={12}>
            <Button
              type="primary"
              size="small"
              onClick={downloadImage}
              //   style={{
              //     height: "50px",
              //     marginTop: "5px",
              //     marginLeft: "10px",
              //     marginBottom: "10px",
              //   }}
            >
              Download QR{" "}
            </Button>
          </Col>
        </Row>
      </Form>
    </>
  );
}

export default ViewQrCode;
