import {
  Row,
  Col,
  Card,
  Form,
  Tag,
  Radio,
  Table,
  Space,
  Progress,
  Checkbox,
  Modal,
  Button,
  Avatar,
  Select,
  Input,
  Typography,
} from "antd";
import Axios from "axios";

import { useState, useEffect } from "react";
import { SearchOutlined, EyeOutlined } from "@ant-design/icons";
import { Chip, Icon, IconButton } from "@mui/material";
import { BASE_URL } from "../../utills/appConstant";

function ManageSettlement() {
  const [APIData, setAPIData] = useState([]);

  const columns = [
    {
      title: "Payment Link Id",
      dataIndex: "id",
      key: "id",
    },
    {
      title: "Amount",
      dataIndex: "amount",
      key: "amount",
    },
    {
      title: "Fees",
      dataIndex: "fee",
      key: "fee",
    },

    {
      title: "Tax",
      dataIndex: "tax",
      key: "tax",
    },
    {
      title: "UTR",
      dataIndex: "utr",
      key: "utr",
    },
    {
      title: "Status",
      key: "status",
      dataIndex: "status",
    },
    // {
    //   title: "Action",
    //   key: "action",
    //   dataIndex: "action",
    // },
  ];

  const getData = async () => {
    await Axios.get(BASE_URL+"/settlements").then((res) => {
      setAPIData(
        res.data.items.map((row) => ({
          id: <a>{row.id}</a>,
          amount: (
            <>
              ₹ <span>{row.amount / 100}</span>
            </>
          ),
          fees: row.fees,
          tax: row.tax,
          utr: row.utr,
          created_at: row.created_at,
          status: (
            <>
              <Space size="middle">
                {row.status == "processed" ? (
                  <>
                    {" "}
                    <Tag color="green">Processed</Tag>
                  </>
                ) : row.status == "created" ? (
                  <>
                    {" "}
                    <Tag color="blue">Created</Tag>
                  </>
                ) : (
                  <>
                    {" "}
                    <Tag color="red">Failed</Tag>
                  </>
                )}
              </Space>
            </>
          ),
          // action: (
          //   <>
          //     <IconButton onClick={() => handleshow(row)}>
          //       <EyeOutlined style={{ fontSize: "18px" }}></EyeOutlined>
          //     </IconButton>
          //   </>
          // ),
        }))
      );
    });
  };

  useEffect(() => {
    getData();
  }, []);

  return (
    <>
      <Row>
        <Col xl={22} style={{ marginLeft: "10px" }}>
          <Input prefix={<SearchOutlined />} placeholder="Search" />
        </Col>
      </Row>
      <div className="tabled" style={{ marginTop: "30px" }}>
        <Row gutter={[24, 0]}>
          <Col xs="24" xl={24}>
            <Card bordered={false} className="criclebox tablespace mb-24">
              <div className="table-responsive">
                <Table
                  columns={columns}
                  dataSource={APIData}
                  pagination={false}
                  rowKey="id"
                  className="ant-border-space"
                />
              </div>
            </Card>
          </Col>
        </Row>
      </div>
    </>
  );
}

export default ManageSettlement;
