import { useState } from "react";
import { Tabs } from "antd";
import Payment from "./Payment";
import AddBank from "./AddBank";
import BankQR from "./BankQR";
import Setting from "./setting";

const { TabPane } = Tabs;

const Gateway = () => {
  const [key, setKey] = useState("1");
  const callback = (k) => {
      localStorage.setItem("gateway-tab", k)
      setKey(k);


  }
  // const items = [
  //   {
  //     key: "1",
  //     label: `Tab 1`,
  //     children: `Content of Tab Pane 1`,
  //   },
  //   {
  //     key: "2",
  //     label: `Tab 2`,
  //     children: `Content of Tab Pane 2`,
  //   },
  //   {
  //     key: "3",
  //     label: `Tab 3`,
  //     children: `Content of Tab Pane 3`,
  //   },
  //   {
  //     key: "4",
  //     label: `Tab 4`,
  //     children: `Content of Tab Pane 4`,
  //   },
  // ];
  return (
    <>
      <Tabs centered defaultActiveKey={localStorage.getItem("gateway-tab")} onChange={callback}>
        <TabPane tab="Bank Detail's" key="tab-a">
          <AddBank></AddBank>
        </TabPane>
        <TabPane tab="QR Code" key="tab-b">
          <BankQR></BankQR>
        </TabPane>
        <TabPane tab="Payment Link" key="tab-c">
          <Payment></Payment>
        </TabPane>
        <TabPane tab="Setting" key="tab-d">
          <Setting></Setting>
        </TabPane>
      </Tabs>
    </>
  );
};
export default Gateway;
