import {
  Row,
  Col,
  Card,
  Form,
  Radio,
  Table,
  Upload,
  Space,
  message,
  Progress,
  Modal,
  Button,
  Avatar,
  Select,
  Input,
  Typography,
} from "antd";

import { useState, useEffect } from "react";
import Axios from "axios";
import SearchOutlined from "@ant-design/icons/SearchOutlined";
import EditOutlined from "@ant-design/icons/EditOutlined";
import DeleteOutlined from "@ant-design/icons/DeleteOutlined";
import { ToTopOutlined } from "@ant-design/icons";
import { Link } from "react-router-dom";

const { Title } = Typography;
const { TextArea } = Input;

const formProps = {
  name: "file",
  action: "https://www.mocky.io/v2/5cc8019d300000980a055e76",
  headers: {
    authorization: "authorization-text",
  },
  onChange(info) {
    if (info.file.status !== "uploading") {
      console.log(info.file, info.fileList);
    }
    if (info.file.status === "done") {
      message.success(`${info.file.name} file uploaded successfully`);
    } else if (info.file.status === "error") {
      message.error(`${info.file.name} file upload failed.`);
    }
  },
};
// table code start

function Catalogue() {
  // const onChange = (e) => console.log(`radio checked:${e.target.value}`);
  const [state, setstate] = useState([]);
  const [loading, setloading] = useState(true);

  useEffect(() => {
    getData();
  }, []);

  const base_url = "http://43.204.212.53:5002";
  const getData = async () => {
    Axios.defaults.headers.post['Content-Type'] ='application/json;charset=utf-8';
    Axios.defaults.headers.post['Access-Control-Allow-Origin'] = '*';
    await Axios.post(base_url + "/services/get_catalogue").then((res) => {
      // setloading(false);

      setstate(
        res.data.data.map((row) => ({
          catalogue_name: row.catalogue_name,
          duration: row.duration,
          price: row.price,
          type: row.type,
          id: row.id,
        }))
      );
      console.log(state);
    });
  };
  const columns = [
    {
      title: "Sr No",
      dataIndex: "id",
      key: "1",
    },
    {
      title: "Catalogue Name",
      dataIndex: "catalogue_name",
      key: "2",
    },
    {
      title: "Duration",
      dataIndex: "duration",
      key: "3",
    },
    {
      title: "Type",
      dataIndex: "type",
      key: "4",
    },

    {
      title: "Price",
      key: "5",
      dataIndex: "price",
    },
    {
      title: "Action",
      key: "action",
      render: (record) => {
        return (
          <>
            <EditOutlined style={{ color: "#1890ff" }} onClick={showModal} />
            &nbsp; &nbsp;
            <DeleteOutlined
              onClick={(e) => {
                // handleDelete(record,e);
              }}
              style={{ color: "red" }}
            />
          </>
        );
      },
    },
  ];
  const [isModalOpen, setIsModalOpen] = useState(false);
  const option = [
    { value: "Equity", label: "Equity" },
    { value: "Future", label: "Future" },
    { value: "Options", label: "Options" },
  ];
  const showModal = () => {
    setIsModalOpen(true);
  };

  const handleOk = () => {
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };
  const data = [
    {
      key: "1",
      name: (
        <>
          <div className="avatar-info">
            <Title level={5}>Cash</Title>
          </div>
        </>
      ),
      type: (
        <>
          <div className="author-info">
            <Title level={5}>Equity</Title>
          </div>
        </>
      ),
      duration: (
        <>
          <div className="author-info">
            <Title level={5}>30 Days [1 Month]</Title>
          </div>
        </>
      ),
      price: (
        <>
          <div className="author-info">
            <Title level={5}>8000</Title>
          </div>
        </>
      ),
    },

    {
      key: "2",
      name: (
        <>
          <div className="avatar-info">
            <Title level={5}>Delivery</Title>
          </div>
          {/* </Avatar.Group>{" "} */}
        </>
      ),
      type: (
        <>
          <div className="author-info">
            <Title level={5}>Equity</Title>
          </div>
        </>
      ),
      duration: (
        <>
          <div className="author-info">
            <Title level={5}>90 Days [3 Month]</Title>
          </div>
        </>
      ),
      price: (
        <>
          <div className="author-info">
            <Title level={5}>12000</Title>
          </div>
        </>
      ),
    },

    {
      key: "3",
      name: (
        <>
          <div className="avatar-info">
            <Title level={5}>Index Option</Title>
          </div>
        </>
      ),
      type: (
        <>
          <div className="author-info">
            <Title level={5}>Index</Title>
          </div>
        </>
      ),
      duration: (
        <>
          <div className="author-info">
            <Title level={5}>180 Days [6 Month]</Title>
          </div>
        </>
      ),
      price: (
        <>
          <div className="author-info">
            <Title level={5}>40000</Title>
          </div>
        </>
      ),
    },
  ];

  // const handleDelete = (id) => {
  //   Axios
  //     .post(base_url+'/services/delete_catalogue',{id: id })
  //     .then((result) => {
  //       setFetchStatus(true);
  //     })
  //     .catch((err) => {
  //       console.log(err);
  //     });
  // };
  return (
    <>
      <Row>
        <Col>
          <Link to={"/addCatalogue"}>
            <Button type="primary" size="large" style={{ height: "50px" }}>
              Add{" "}
            </Button>
          </Link>
        </Col>
        <Col xl={21} style={{ marginLeft: "10px" }}>
          <Input prefix={<SearchOutlined />} placeholder="Search" />
        </Col>
      </Row>
      <div className="tabled" style={{ marginTop: "30px" }}>
        <Row gutter={[24, 0]}>
          <Col xs="24" xl={24}>
            <Card
              bordered={false}
              className="criclebox tablespace mb-24"
              title="Catalogues"
            >
              <div className="table-responsive">
                {/* {loading ? (
                  "Loading"
                ) : ( */}
                <Table
                  columns={columns}
                  dataSource={state}
                  pagination={{ pageSize: 15, marginLeft: "12px" }}
                  className="ant-border-space"
                />
                {/* )} */}
              </div>
            </Card>
          </Col>
        </Row>
      </div>
      <Modal
        title="Edit Catalogue"
        okText="Update"
        width={600}
        visible={isModalOpen}
        onOk={handleOk}
        onCancel={handleCancel}
      >
        <Form>
          <Row>
            <Col xl={12} style={{ marginTop: "7px" }}>
              <label>Name</label>
              <Input placeholder="Enter Name" />
            </Col>
            <Col xl={12}>
              <p style={{ marginLeft: "13px" }}>Type</p>
              <Select
                placeholder="Select Type"
                size="large"
                options={option}
                style={{ marginLeft: "15px", width: 270, marginTop: "-14px" }}
              />
            </Col>
          </Row>
          <Row style={{ marginTop: "20px" }}>
            <Col xl={12}>
              <label>Duration</label>
              <Input placeholder="Enter Duration" />
            </Col>
            <Col xl={12}>
              <label style={{ marginLeft: "13px" }}>Price</label>
              <Input placeholder="Enter Price" style={{ marginLeft: "13px" }} />
            </Col>
          </Row>
          <Row style={{ marginTop: "30px" }}>
            <Col xl={24}>
              <label style={{ marginLeft: "13px" }}>Description</label>
              <TextArea placeholder="Enter Description" rows={6} />
            </Col>
          </Row>
        </Form>
      </Modal>
    </>
  );
}

export default Catalogue;
