import {
    Row,
    Col,
    Card,
    Form,
    Tag,
    Radio,
    Table,
    Upload,
    Space,
    message,
    Progress,
    DatePicker,
    Modal,
    Button,
    Avatar,
    Select,
    Input,
    Typography,
} from "antd";
import { useEffect, useState } from "react";
import Axios from "axios";
import SearchOutlined from "@ant-design/icons/SearchOutlined";
import EditOutlined from "@ant-design/icons/EditOutlined";
import DeleteOutlined from "@ant-design/icons/DeleteOutlined";
import { Link } from "react-router-dom";
import { BASE_URL } from "../../utills/appConstant";

const { Title } = Typography;

function ManageBranchMaster() {
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [APIData, setAPIData] = useState([])
    const base_url = ''

    

    const columns = [
        {
            title: "Sr No",
            dataIndex: "id",
            key: "id",
        },
        {
            title: "Branch Name",
            dataIndex: "name",
            key: "name",
        },        
        {
            title: "Action",
            key: "action",
            dataIndex: "action",
        },
    ];
    const getData = async () => {
        await Axios.post(BASE_URL + "/services/get_branch_master").then(
            (res) => {
                setAPIData(
                    res.data.data.map((row) => ({
                        name: row.name,
                        id: row.id,
                    }))
                );
                console.log(APIData)
            }
        );
    };
    useEffect(() => {
        getData();
    }, []);
    const data = [
        {
            key: "1",
            brmanager: (
                <>
                    {/* <Avatar.Group>
                  <Avatar
                    className="shape-avatar"
                    shape="square"
                    size={40}
                    src={face2}
                  ></Avatar> */}
                    <div className="avatar-info">
                        <Title level={5}>Thane</Title>
                    </div>
                    {/* </Avatar.Group>{" "} */}
                </>
            ),

            cname: (
                <>
                    <div className="author-info">
                        <Title level={5}>0.8 %</Title>
                    </div>
                </>
            ),
            amount: (
                <>
                    <div className="author-info">
                        <Tag color="green">Active</Tag>
                    </div>
                </>
            ),

            action: (
                <>
                    <Space wrap>
                        <EditOutlined style={{ color: "#1890ff" }} />
                        <DeleteOutlined style={{ color: "red" }} />
                    </Space>
                </>
            ),
        },

        {
            key: "2",
            brmanager: (
                <>
                    {/* <Avatar.Group>
                  <Avatar
                    className="shape-avatar"
                    shape="square"
                    size={40}
                    src={face2}
                  ></Avatar> */}
                    <div className="avatar-info">
                        <Title level={5}>Kalyan</Title>
                    </div>
                    {/* </Avatar.Group>{" "} */}
                </>
            ),

            cname: (
                <>
                    <div className="author-info">
                        <Title level={5}>1 %</Title>
                    </div>
                </>
            ),
            amount: (
                <>
                    <div className="author-info">
                        <Tag color="orange">Closed</Tag>
                    </div>
                </>
            ),

            date: (
                <>
                    <div className="author-info">
                        <Title level={5}>2023-01-17</Title>
                    </div>
                </>
            ),
            mode: (
                <>
                    <div className="author-info">
                        <Title level={5}>Cash</Title>
                    </div>
                </>
            ),
            action: (
                <>
                    <Space wrap>
                        <EditOutlined style={{ color: "#1890ff" }} />
                        <DeleteOutlined style={{ color: "red" }} />
                    </Space>
                </>
            ),
        },
    ];
    const showModal = () => {
        setIsModalOpen(true);
    };

    const handleOk = () => {
        setIsModalOpen(false);
    };

    const handleCancel = () => {
        setIsModalOpen(false);
    };

    /**
     * Add Branch Master
     */
    const [name, setName] = useState('')
    const postData = async () => {
        const branchData = {
            name: name
        }
        await Axios.post(BASE_URL + "/services/add_branch_master", branchData)
    };
    const [validated, setValidated] = useState(false);
    const handleSubmit = (event) => {
        const form = event.currentTarget;
        if (form.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
            alert("Please Provide appropriate Data")
        } else if (form.checkValidity() != false) {
            postData();
            // changePage();
            setIsModalOpen(false);
        }
        setValidated(true);
    };
    return (
        <>
            <Row>
                <Col>
                    <Button
                        type="primary"
                        size="large"
                        onClick={showModal}
                        style={{ height: "50px" }}
                    >
                        Add Branch
                    </Button>
                </Col>
                <Col xl={21} style={{ marginLeft: "10px" }}>
                    <Input prefix={<SearchOutlined />} placeholder="Search" />
                </Col>
            </Row>
            <div className="tabled" style={{ marginTop: "30px" }}>
                <Row gutter={[24, 0]}>
                    <Col xs="24" xl={24}>
                        <Card
                            bordered={false}
                            className="criclebox tablespace mb-24"
                        >
                            <div className="table-responsive">
                                <Table
                                    columns={columns}
                                    dataSource={APIData}
                                    pagination={false}
                                    className="ant-border-space"
                                />
                            </div>
                        </Card>
                    </Col>
                </Row>
            </div>
            <Modal
                title="Add Branch Master"
                okText="ADD BR"
                width={600}
                visible={isModalOpen}
                onOk={handleSubmit}
                onCancel={handleCancel}
            >
                <Form>
                    <Row>
                        <Col style={{ marginTop: "7px" }}>
                            <label>Branch Name</label>
                            <Input
                                required
                                value={name}
                                onChange={(e) => setName(e.target.value)}
                                placeholder="Enter Name"
                            />
                        </Col>
                    </Row>
                </Form>
            </Modal>
        </>
    );
}
export default ManageBranchMaster;