import {
    Row,
    Col,
    Card,
    Form,
    Tag,
    Radio,
    Table,
    Upload,
    Space,
    message,
    Progress,
    DatePicker,
    Modal,
    Button,
    Avatar,
    Select,
    Input,
    Typography,
} from "antd";
import { useEffect, useState } from "react";
import Axios from "axios";
import SearchOutlined from "@ant-design/icons/SearchOutlined";
import EditOutlined from "@ant-design/icons/EditOutlined";
import DeleteOutlined from "@ant-design/icons/DeleteOutlined";
import { Link } from "react-router-dom";
import { BASE_URL } from "../../utills/appConstant";

const { Title } = Typography;

function ManageDemo() {
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [APIData, setAPIData] = useState([])
    const [TableHeaders, setTableHeaders] = useState([])
    const base_url = 'http://localhost:3000/matrixs'



    const columns = [
        {
            title: "Sr.No",
            dataIndex: "id",
            key: "id",
        },
        {
            title: "Branch Name",
            dataIndex: "name",
            key: "name",
        },

        {
            title: "Action",
            key: "action",
            dataIndex: "action",
        },
    ];
    const getData = async () => {
        await Axios.get(base_url).then(
            (res) => {
                getCloumn(res.data)
            }
        );
    };
    const getCloumn = (data) => {
        var array = [{ title: "", key: "", dataIndex: "" }]
        for (var i = 0; i < data.length; i++) {
            var obj1 = {
                title: data[i].start_Slab + "-" + data[i].end_Slab,
                key: data[i].start_Slab + "-" + data[i].end_Slab,
                dataIndex: data[i].start_Slab + "-" + data[i].end_Slab
            }
            array.push(obj1)
        }
        setTableHeaders(array)
    }
    useEffect(() => {
        getData();
    }, [APIData]);

    return (
        <>
            <div className="tabled" style={{ marginTop: "30px" }}>
                <Row gutter={[24, 0]}>
                    <Col xs="24" xl={24}>
                        <Card
                            bordered={false}
                            className="criclebox tablespace mb-24"
                        >
                            <div className="table-responsive">
                                <Table
                                    columns={TableHeaders}
                                    dataSource={APIData}
                                    pagination={false}
                                    className="ant-border-space"
                                />
                            </div>
                        </Card>
                    </Col>
                </Row>
            </div>

        </>
    );
}
export default ManageDemo;