import {
  Row,
  Col,
  Card,
  Form,
  Tag,
  Radio,
  Table,
  Upload,
  Space,
  message,
  Progress,
  Checkbox,
  Modal,
  Button,
  Avatar,
  Select,
  Input,
  Typography,
} from "antd";
import moment from "moment";
import Axios from "axios";
import { Chip } from "@mui/material";
import { useState, useEffect } from "react";
import { BASE_URL } from "../../utills/appConstant";

const { Title } = Typography;

function Notify({ NotifyData, handleCancel }) {
  const close = () => {
    handleCancel();
  };
  const [medium] = useState("email,sms");
  const option = [
    { value: "email", label: "Email" },
    { value: "sms", label: "SMS" },
  ];
  const sendLink = () => {
    Axios.post(
      BASE_URL+"/payment_links/" +
        NotifyData.id +
        "/notify_by/" +
        medium
    );
    alert("Message Send Successfuly");
    handleCancel();
  };
  return (
    <>
      <Form>
        <Row>
          <Col xl={12}>
            <label>Amount</label>
          </Col>
          <Col xl={12}>
            <label>₹ {NotifyData.amount / 100}</label>
          </Col>
        </Row>
        <br></br>
        <Row>
          <Col xl={12}>
            <label>Customer Details</label>
          </Col>
          <Col xl={12}>
            <label>{NotifyData.customer.name}</label>
            <br></br>
            <label>{NotifyData.customer.email}</label>
            <br></br>
            <label>{NotifyData.customer.contact}</label>
          </Col>
        </Row>
        {/* <br></br>
        <p style={{ marginLeft: "1px" }}>Select the Medium</p>
        <Select
          placeholder="Select Type"
          size="large"
          defaultValue="Select QR ID"
          options={option}
          value={medium}
          onChange={(e) => {
            setMedium(e);
          }}
          style={{ marginLeft: "1px", width: 230, marginTop: "-14px" }}
        ></Select> */}
        <br></br>
        <div style={{ textAlign: "end" }}>
          <Button onClick={close}>Cancel</Button>&nbsp;
          <Button type="primary" onClick={sendLink}>
            Send
          </Button>
        </div>
      </Form>
    </>
  );
}

export default Notify;
